export default {
	data:function(){
	 return {
	   inputValue:'',
	   psdValue:'',
	   username:'',
	   avtar_url:''
	 }	
	},
	methods:{
		dl:function(){
			if(this.inputValue.length!=0&&this.psdValue.length!=0){
				uni.showToast({
					title:"登录成功",																								
				})
				setTimeout(function(){
					uni.navigateBack({
						
					})
				},2000)
			}
		},
		bl:function(event){
			this.inputValue=event.target.value
			if(this.inputValue.length==0){
				uni.showToast({
					title:"用户名不得为空"
				})
			}
		},
		psdbl:function(event){
			this.psdValue=event.target.value
			if(this.psdValue.length==0){
				uni.showToast({
					title:"密码不得为空"
				})
			}
		},
		wx_login:function(){
			uni.getProvider({
				service:"oauth",
				success: (loginRes) => {
					uni.login({							
					provider: "weixin",				
					success:(loginRes)=>{
						uni.showToast({
							title:"微信登录成功"
						})
						
						uni.getUserInfo({
							provider:"weixin",
							withCredentials:"true",
							success: (loginRes) => {
								this.username=loginRes.userInfo.nickName
								this.avtar_url=loginRes.userInfo.avatarUrl
								uni.setStorage({
									key:"login_info",
									data:{username:this.username,avtarurl:this.avtar_url}
								})
								
							}
						})
						setTimeout(function(){
							uni.navigateBack({
								
							})
						},1000)
						
						},
						fail: (loginRes) => {
							uni.showToast({
								title:"微信登录失败"
							})
						}
						})
				},
				fail: (loginRes) => {
					uni.showToast({
						title:"未检测到微信"
					})
				}
			})
													
		},
		qq_login:function(){
			uni.showToast({
				title:"正在开发中",
				icon:"none"
			})
		},
		wb_login:function(){
			uni.showToast({
				title:"正在开发中",
				icon:"none"
			})
		}
	}
	
		
	
}
